/* ปิด Scroll ทุกกรณี */
html, body {
    margin: 0;
    padding: 0;
    overflow: hidden; /* ไม่ให้เลื่อน */
    background-color: #FFFCE0;
    
}


.home-container {
    width: 100vw;
    display: flex;
    flex-direction: column; /* แยก header กับ artwork */
    justify-content: flex-start; /* header อยู่บนสุด */
    background-color: #FFFCE0;
}

/* ส่วน Header */
.header {
    position: relative;
    width: 100%;
    height: 50vh;
    background-image: url("../assets/bg.png");
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: height 0.5s ease-in-out; /* เฉพาะ background ขยาย */
}

/* ขยาย background ลงล่าง */
.header.expanded {
    height: 100vh;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 20px;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(48, 17, 14, 0.8);
}

/* โลโก้ */
.logo {
    position: relative;
    z-index: 1;
    max-width: 20vw;
}

/* โลโก้ขึ้นไปชิดบนเมื่อขยาย */
.header.expanded .logo {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
}

/* ข้อความที่แสดงเมื่อขยาย */
.info-text {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    font-size: 3vh;
    font-weight: bold;
    z-index: 2;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    text-align: center;
}

.header.expanded .info-text {
    opacity: 1; /* แสดงข้อความเมื่อขยาย */
}

/* ปุ่ม info */
.info-btn {
    position: absolute;
    width: 3vw;
    height: 3vw;
    bottom: 30px;
    right: 20px;
    background-color: #F0BB7C;
    border: none;
    padding: 10px 15px;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 5px 0px 0px #C8622E;
}

.info-btn-icon {
    color: white;
    font-size: 1vw;
}

.artwork-btn {
    display: flex;
    padding: 10px 20px;
    
}

.art-work {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 100%;
}

.art-image {
    width: 20vw;
    height: 40vh;
    background-size: cover;
    background-position: center;
    border-radius: 20px;
}

/* ปุ่มซ้ายขวา */
.arrow-btn {
    transform: translateY(-50%);
    background-color: #89431E;
    color: white;
    border: none;
    padding: 15px; /* ปรับขนาดของปุ่ม */
    font-size: 2vw;
    cursor: pointer;
    z-index: 2;
    box-shadow: 0px 5px 0px 0px #4B1C09;
    border-radius: 50%; /* ทำให้ปุ่มเป็นวงกลม */
    width: 3vw; /* กำหนดขนาดของปุ่ม */
    height: 3vw; /* กำหนดขนาดของปุ่ม */
    display: flex;
    justify-content: center;
    align-items: center;
}
.arrow-btn.left {
    left: 10px;
}

.arrow-btn.right {
    right: 10px;
}

h1 {
    margin: 0;
    padding: 20px 20px 0px 20px;
    color: #4B1C09;
    font-family: Lilita One;
    font-weight: 400;
    font-size: 2vw;
    line-height: 100%;
    letter-spacing: 0%;

}

.all-btn {
    width: 100%;
    display: flex;
    flex-direction: column; /* เรียงปุ่มในแนวตั้ง */
    gap: 30px; /* ระยะห่างระหว่างปุ่ม */
    align-items: center; /* จัดกึ่งกลางปุ่ม */
    justify-content: center;
}

/* สำหรับปุ่มใน .all-contact ให้ flex กัน */
.all-contact {
    display: flex;
    gap: 20px; /* ระยะห่างระหว่างปุ่ม */
    justify-content: center; /* จัดกึ่งกลาง */
    width: 100%;
}

.facebook {
    box-shadow: 0px 8px 2px 0px #58269B;
    border: 1px solid #58269B;
    background: #3765E8;
    border-radius: 100%; /* ทำให้ปุ่มเป็นวงกลม */
    width: 6vw; /* กำหนดขนาดของปุ่ม */
    height: 6vw; /* กำหนดขนาดของปุ่ม */
    box-sizing: border-box;
}

.gmail {
    box-shadow: 0px 8px 2px 0px #9F174B;
    border: 1px solid #9F174B;    
    background: #F84967;
    border-radius: 100%; /* ทำให้ปุ่มเป็นวงกลม */
    width: 6vw; /* กำหนดขนาดของปุ่ม */
    height: 6vw; /* กำหนดขนาดของปุ่ม */
    box-sizing: border-box;
}

.line {
    box-shadow: 0px 8px 2px 0px #246880;
    border: 1px solid #246880;
    border-radius: 100%; /* ทำให้ปุ่มเป็นวงกลม */
    width: 6vw; /* กำหนดขนาดของปุ่ม */
    height: 6vw; /* กำหนดขนาดของปุ่ม */
    background: #B4EB16;
    box-sizing: border-box;
}

.icon{
    color: white;
    font-size: 3vw;
}

.play {
 width: 50%;
 background: #FFE13A;
 border: 1px solid #A60D2B;
 box-shadow: 0px 10px 4px 0px #AA3150;
 font-family: Lilita One;
 font-size: 5vh;
 color: #A60D2B;
 text-shadow: 0px 4px 4px 0px #00000040 inset;
 border-radius: 10px;
 padding: 10px;
}

.survey {
    width: 50%;
    background: #E7E7D7;
    border: 1px solid #697174;
    box-shadow: 0px 10px 4px 0px #AA3150;
    font-family: Lilita One;
    font-size: 5vh;
    color: #545555;
    box-shadow: 0px 10px 4px 0px #697174;
    border-radius: 10px;
    padding: 10px;
    text-shadow: 0px 4px 4px 0px #00000040 inset;
}



/* สำหรับแท็บเล็ตและอุปกรณ์ที่มีหน้าจอขนาด 1024px หรือเล็กกว่า */
@media (max-width: 1024px) {
    html, body {
        overflow: auto; /* ให้เลื่อนได้ในมือถือ */
    }
    /* ลดขนาดของ header */
    .header {
        height: 55vh; 
    }

    .header.expanded {
        height: 90vh; /* ปรับให้ไม่เต็มหน้าจอเกินไป */
    }

    /* ปรับขนาดฟอนต์ในมือถือ */
    .info-text {
        font-size: 3vw; /* ปรับขนาดฟอนต์ */
        width: 80%;
        top: 35%;
    }

    .logo {
        max-width: 250px; /* ลดขนาดโลโก้ */
    }

    h1 {
        text-align: center;
        font-size: 4vw; /* ปรับขนาดฟอนต์ h1 */
    }

    .all-btn {
        padding-top: 30px;
        padding-bottom: 10px;
    }

    .art-image {
        width: 50vw;
        height: 30vh;
    }
    .artwork-btn {
        flex-direction: column; /* เรียงปุ่มในแนวตั้ง */
    }
    
    .arrow-btn {
        width: 6vw;
        height: 6vw;
    }

    .play , .survey {
        font-size: 4vh;
    }

    .facebook , .line , .gmail {
        width: 8vw;
        height: 8vw;
    }

    .info-btn {
        width: 8vw;
        height: 8vw;
    }

    .info-btn-icon {
        font-size: 4vw;
        
    }
}

/* สำหรับมือถือ (ขนาดหน้าจอ 768px หรือน้อยกว่า) */
@media (max-width: 768px) {
    /* ปรับให้เลื่อนหน้าเว็บได้เมื่อขยาย */

    .header {
        height: 60vh; /* ลดขนาดของ header */
    }

    .header.expanded {
        height: 100vh; /* ขยายเต็มหน้าจอ */
    }

    /* ปรับฟอนต์ให้เล็กลงในมือถือ */
    .info-text {
        font-size: 18px;  /* ปรับขนาดฟอนต์ */
        top: 35%;
        width: 60%;

    }

    .logo {
        max-width: 200px; /* ลดขนาดโลโก้ */
    }

    h1 {
        font-size: 20px; /* ปรับขนาดฟอนต์ h1 */
    }

    /* เพิ่มขนาดฟอนต์ในปุ่ม */
    .play, .survey {
        font-size: 30px; /* ปรับขนาดฟอนต์ */
    }

    /* เพิ่มระยะห่างระหว่างปุ่ม */
    .all-btn {
        gap: 20px; /* เพิ่มระยะห่างระหว่างปุ่ม */
    }

    .artwork-btn {
        flex-direction: column; /* เรียงปุ่มในแนวตั้ง */
    }

    .art-image {
        width: 100%;
        height: 40vh;
    }

    /* ปรับขนาดของปุ่มโซเชียล */
    .facebook, .gmail, .line {
        width: 10vw; /* กำหนดขนาดของปุ่ม */
        height: 10vw; /* กำหนดขนาดของปุ่ม */
    }

    .icon {
        font-size: 3vw;
    }
    
    .arrow-btn {
        width: 5vw;
        height: 5vw;
    }

     .info-btn-icon {
        font-size: 3vw;
     }
     .info-btn {
        width:6vw;
        height: 6vw;
     }
}

@media (max-width: 425px) {
    .art-work {
        gap: 5px;
    }

    .art-image  {
        height: 20vh;
    }
    .info-btn-icon {
        font-size: 5vw;
     }
     .info-btn {
        width:8vw;
        height: 8vw;
     }

}